.wish{
    max-width: 25%;
}

.wishName{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    line-height: 19px;
    color: #FFFEFE;
    margin-bottom: 8px;
}

.wishDescription{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    color: #FFFEFE;
    overflow: auto;
    height: 79px;
}

.wish-author, .wishCategory, .wish-price, .wish-link{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 10px;
    line-height: 12px;
    color: #FFFEFE;
    margin-bottom: 5px;
}