.authAndRegForm{
    width: 43.4%;
    max-width: 418px;
    margin: 0 auto;
}

.form-half{
    background: rgba(62, 7, 67, 0.8);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.4);
}

.headline-half{
    margin-bottom: 13px;
}

.main-lalf{
    padding: 30px 12px 25px 12px;
}

.form-headline{
    font-family: "Roboto";
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    text-align: center;
    color: #FFF7F7;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);   
}

.form-actions{
    padding-top: 18px;
    display: flex;
    justify-content: space-around;
}

.authAndRegForm__to-login-link{
    font-family: "Roboto";
    font-style: normal;
    font-size: 12px;
    text-align: center;
    color: #FFF7F7;
    text-decoration: underline;
    display: block;
    margin-top: 8px;
}