.header{
    background: rgba(62, 7, 67, 0.16);
    box-shadow: 0px 4px 5px rgba(5, 1, 35, 0.81);
    padding-top: 8px;
    padding-bottom: 8px;
    opacity: 0.9;
}

.header_not-authorized{
    margin-bottom: 40px;
}

.header_authorized{
    display: flex;
    padding-left: 15px;
    padding-right: 15px;
}

.header__menu{
    display:flex;
    align-items: center;
}

.header__menu-item{
    height: min-content;
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    mix-blend-mode: normal;
    opacity: 0.9;
    text-shadow: 0px 4px 4px #000000;
    color: #F9EFFA;
}

.header__username{
    display: block;
    margin-left: auto;
}

.header__firstname, .header__lastname{
    text-align: left;
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    color: #FFF7F7;
}

.gretting-text{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    text-align: center;
    color: #F9EFFA;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 8px;
}