.create-wish-form{
    margin: 0 auto;
    display: block;
    max-width: 60%;
    max-height: max-content;
    padding: 20px;
    background: rgba(62, 7, 67, 0.5);
}

.create-wish-form .form-field{
    width: 80%;
    margin:0 auto;
    margin-bottom: 20px;
    text-align: left;
}

.create-wish-form .form-field .form-field__name{
    text-align: left;
}

.create-wish-form .form-field .form-field__container{
    width: auto;
}

.create-wish-form__manage-section{
    margin-left: auto;
    max-width: 40%;
    display: flex;
    justify-content: space-around;
}