.form-field{
    width: 100%;
    display: flex;
    margin-bottom: 20px;
}

.form-field__container{
    display: flex;
    width: 60%;
    margin-right: 0;
    margin-left: auto;
    position: relative;
}

.form-field__name{
    font-family: "Roboto";
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    text-align: center;
    color: #F9EFFA;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.form-field__value{
    background: rgba(186, 177, 240, 1);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: "Roboto";
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
    padding-left: 6px;
    border: 0px;
}

.form-field__value::-webkit-input-placeholder{
    color: #FFFFFF;
}

.form-field__value:focus{
    outline: 0px;
    border: 0px;
}

.form-field__error{
    border: 1px solid #E03658 !important;
}

.form-field__error::-webkit-input-placeholder{
    color: #E03658;
    font-size: 12px;
}

.form-field__error-text{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    text-align: left;
    font-size: 10px;
    line-height: 10px;
    color: #E03658;
    top:25px;
    position:absolute;
}